import React, { useState, useEffect } from 'react'
import { XAxis, YAxis, CartesianGrid, Tooltip, BarChart, Bar, PieChart, Pie, Cell, ResponsiveContainer } from 'recharts'
import { CSVLink } from 'react-csv'
import { Search, PlusCircle, Trash2, MinusCircle, PlusSquare, AlertTriangle, DollarSign, Package, ShoppingCart, BarChart2, PieChart as PieChartIcon, Settings, Filter, Truck } from 'lucide-react'

function InventoryManagementSystem() {
  const [productos, setProductos] = useState([])
  const [categorias, setCategorias] = useState([])
  const [ventas, setVentas] = useState([])
  const [proveedores, setProveedores] = useState([])
  const [reabastecimientos, setReabastecimientos] = useState([])

  const [nuevoProducto, setNuevoProducto] = useState({
    ID_Producto: '',
    Nombre_Producto: '',
    Descripcion: '',
    Precio: 0,
    Cantidad: 0,
    ID_Categoria: ''
  })

  const [searchTerm, setSearchTerm] = useState('')
  const [activeTab, setActiveTab] = useState('inventory')
  const [lowStockThreshold, setLowStockThreshold] = useState(5)
  const [settings, setSettings] = useState({ taxRate: 19, currency: 'CLP' })
  const [categoryFilter, setCategoryFilter] = useState('Todos')

  useEffect(() => {
    // Funcin para cargar datos desde localStorage o usar datos de ejemplo
    const cargarDatos = (key, datosEjemplo) => {
      const datosAlmacenados = localStorage.getItem(key);
      if (datosAlmacenados) {
        return JSON.parse(datosAlmacenados);
      } else {
        localStorage.setItem(key, JSON.stringify(datosEjemplo));
        return datosEjemplo;
      }
    };

    // Datos de ejemplo
    const productosEjemplo = [
      { ID_Producto: '1', Nombre_Producto: 'Coca-Cola', Descripcion: 'Refresco de cola', Precio: 1500, Cantidad: 100, ID_Categoria: '1' },
      { ID_Producto: '2', Nombre_Producto: 'Papas Fritas', Descripcion: 'Snack salado', Precio: 1000, Cantidad: 50, ID_Categoria: '2' },
      { ID_Producto: '3', Nombre_Producto: 'Agua Mineral', Descripcion: 'Agua sin gas', Precio: 800, Cantidad: 150, ID_Categoria: '1' },
      { ID_Producto: '4', Nombre_Producto: 'Chocolate', Descripcion: 'Barra de chocolate con leche', Precio: 1200, Cantidad: 80, ID_Categoria: '3' },
      { ID_Producto: '5', Nombre_Producto: 'Galletas', Descripcion: 'Paquete de galletas surtidas', Precio: 1500, Cantidad: 60, ID_Categoria: '3' },
    ];

    const categoriasEjemplo = [
      { ID_Categoria: '1', Nombre_Categoria: 'Bebidas' },
      { ID_Categoria: '2', Nombre_Categoria: 'Snacks' },
      { ID_Categoria: '3', Nombre_Categoria: 'Dulces' },
    ];

    const proveedoresEjemplo = [
      { ID_Proveedor: '1', Nombre_Proveedor: 'Distribuidora Nacional', Contacto_Proveedor: 'contacto@disnac.com' },
      { ID_Proveedor: '2', Nombre_Proveedor: 'Importadora del Sur', Contacto_Proveedor: 'ventas@impsur.cl' },
    ];

    const ventasEjemplo = [
      { ID_Venta: '1', Fecha_Venta: '2023-05-01T10:00:00', Total_Venta: 3000, ID_Producto: '1', Cantidad_Vendida: 2 },
      { ID_Venta: '2', Fecha_Venta: '2023-05-02T15:30:00', Total_Venta: 2000, ID_Producto: '2', Cantidad_Vendida: 2 },
    ];

    const reabastecimientosEjemplo = [
      { ID_Reabastecimiento: '1', Fecha_Reabastecimiento: '2023-04-30', ID_Producto: '1', Cantidad_Reabastecida: 50, ID_Proveedor: '1' },
      { ID_Reabastecimiento: '2', Fecha_Reabastecimiento: '2023-05-01', ID_Producto: '2', Cantidad_Reabastecida: 100, ID_Proveedor: '2' },
    ];

    // Cargar datos
    setProductos(cargarDatos('productos', productosEjemplo));
    setCategorias(cargarDatos('categorias', categoriasEjemplo));
    setProveedores(cargarDatos('proveedores', proveedoresEjemplo));
    setVentas(cargarDatos('ventas', ventasEjemplo));
    setReabastecimientos(cargarDatos('reabastecimientos', reabastecimientosEjemplo));

    // Cargar configuracin
    const settingsAlmacenados = localStorage.getItem('settings');
    if (settingsAlmacenados) {
      setSettings(JSON.parse(settingsAlmacenados));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('productos', JSON.stringify(productos))
    localStorage.setItem('categorias', JSON.stringify(categorias))
    localStorage.setItem('ventas', JSON.stringify(ventas))
    localStorage.setItem('proveedores', JSON.stringify(proveedores))
    localStorage.setItem('reabastecimientos', JSON.stringify(reabastecimientos))
    localStorage.setItem('settings', JSON.stringify(settings))
  }, [productos, categorias, ventas, proveedores, reabastecimientos, settings])

  const addProducto = () => {
    if (nuevoProducto.Nombre_Producto && nuevoProducto.Precio > 0 && nuevoProducto.Cantidad >= 0) {
      setProductos([...productos, { ...nuevoProducto, ID_Producto: Date.now().toString() }])
      setNuevoProducto({
        ID_Producto: '',
        Nombre_Producto: '',
        Descripcion: '',
        Precio: 0,
        Cantidad: 0,
        ID_Categoria: ''
      })
    }
  }

  const deleteProducto = (id) => {
    setProductos(productos.filter(producto => producto.ID_Producto !== id))
  }

  const updateCantidad = (id, amount) => {
    setProductos(productos.map(producto =>
      producto.ID_Producto === id ? { ...producto, Cantidad: Math.max(0, producto.Cantidad + amount) } : producto
    ))
    if (amount < 0) {
      const productoVendido = productos.find(producto => producto.ID_Producto === id)
      if (productoVendido) {
        const precioVenta = productoVendido.Precio * (1 + settings.taxRate / 100)
        const nuevaVenta = {
          ID_Venta: Date.now().toString(),
          Fecha_Venta: new Date().toISOString(),
          Total_Venta: -amount * precioVenta,
          ID_Producto: id,
          Cantidad_Vendida: -amount
        }
        setVentas([...ventas, nuevaVenta])
      }
    }
  }

  const filteredProductos = productos.filter(producto =>
    producto.Nombre_Producto.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (categoryFilter === 'Todos' || producto.ID_Categoria === categoryFilter)
  )

  const totalValue = productos.reduce((sum, producto) => sum + producto.Precio * producto.Cantidad, 0)

  const chartData = productos.map(producto => ({
    name: producto.Nombre_Producto,
    value: producto.Cantidad
  }))

  const categoryChartData = Object.entries(
    productos.reduce((acc, producto) => {
      const categoria = categorias.find(cat => cat.ID_Categoria === producto.ID_Categoria)
      const nombreCategoria = categoria ? categoria.Nombre_Categoria : 'Sin categora'
      acc[nombreCategoria] = (acc[nombreCategoria] || 0) + producto.Cantidad
      return acc
    }, {})
  ).map(([name, value]) => ({ name, value }))

  const COLORS = ['#4299E1', '#48BB78', '#F6AD55', '#F56565', '#9F7AEA', '#ED64A6']

  const lowStockProductos = productos.filter(producto => producto.Cantidad <= lowStockThreshold)

  const totalSales = ventas.reduce((sum, venta) => sum + venta.Total_Venta, 0)

  const formatCurrency = (value) => {
    return new Intl.NumberFormat('es-CL', { style: 'currency', currency: 'CLP' }).format(value)
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <nav className="bg-gradient-to-r from-blue-600 to-blue-800 text-white p-4 shadow-lg">
        <h1 className="text-3xl font-bold">Sistema de Gestin de Inventarios</h1>
      </nav>
      
      <div className="container mx-auto p-4">
        <div className="mb-4 flex flex-wrap justify-center space-x-2 space-y-2">
          {['inventory', 'add', 'analytics',   'lowStock', 'sales', 'providers', 'restock', 'settings'].map((tab) => (
            <button
              key={tab}
              className={`px-4 py-2 rounded-md transition-all transform hover:scale-105 ${
                activeTab === tab ? 'bg-blue-600 text-white shadow-md' : 'bg-white text-blue-600 hover:bg-blue-100'
              }`}
              onClick={() => setActiveTab(tab)}
            >
              {tab === 'inventory' && <Package className="inline-block mr-2 h-5 w-5" />}
              {tab === 'add' && <PlusSquare className="inline-block mr-2 h-5 w-5" />}
              {tab === 'analytics' && <BarChart2 className="inline-block mr-2 h-5 w-5" />}
              {tab === 'lowStock' && <AlertTriangle className="inline-block mr-2 h-5 w-5" />}
              {tab === 'sales' && <ShoppingCart className="inline-block mr-2 h-5 w-5" />}
              {tab === 'providers' && <Truck className="inline-block mr-2 h-5 w-5" />}
              {tab === 'restock' && <PlusCircle className="inline-block mr-2 h-5 w-5" />}
              {tab === 'settings' && <Settings className="inline-block mr-2 h-5 w-5" />}
              {tab.charAt(0).toUpperCase() + tab.slice(1)}
            </button>
          ))}
        </div>
        
        {activeTab === 'inventory' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Inventario Actual</h2>
            <div className="flex justify-between items-center mb-4">
              <div className="flex items-center space-x-4">
                <div className="relative">
                  <Search className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
                  <input
                    type="text"
                    placeholder="Buscar productos..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="pl-10 pr-4 py-2 border rounded-md w-64 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>
                <div className="relative">
                  <Filter className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
                  <select
                    value={categoryFilter}
                    onChange={(e) => setCategoryFilter(e.target.value)}
                    className="pl-10 pr-4 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="Todos">Todas las categoras</option>
                    {categorias.map(categoria => (
                      <option key={categoria.ID_Categoria} value={categoria.ID_Categoria}>{categoria.Nombre_Categoria}</option>
                    ))}
                  </select>
                </div>
              </div>
              <CSVLink 
                data={productos} 
                filename={"inventario.csv"} 
                className="bg-green-500 text-white px-4 py-2 rounded-md hover:bg-green-600 transition-colors focus:outline-none focus:ring-2 focus:ring-green-500"
              >
                Exportar CSV
              </CSVLink>
            </div>
            <div className="overflow-x-auto">
              <table className="min-w-full bg-white border border-gray-300">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Nombre</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Descripcin</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Precio</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Cantidad</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Categora</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Acciones</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                  {filteredProductos.map(producto => (
                    <tr key={producto.ID_Producto} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-no-wrap">{producto.Nombre_Producto}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{producto.Descripcion}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{formatCurrency(producto.Precio)}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{producto.Cantidad}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        {categorias.find(cat => cat.ID_Categoria === producto.ID_Categoria)?.Nombre_Categoria}
                      </td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        <button onClick={() => updateCantidad(producto.ID_Producto, -1)} className="text-red-600 hover:text-red-900 mr-2 focus:outline-none">
                          <MinusCircle className="h-5 w-5" />
                        </button>
                        <button onClick={() => updateCantidad(producto.ID_Producto, 1)} className="text-green-600 hover:text-green-900 mr-2 focus:outline-none">
                          <PlusCircle className="h-5 w-5" />
                        </button>
                        <button onClick={() => deleteProducto(producto.ID_Producto)} className="text-red-600 hover:text-red-900 focus:outline-none">
                          <Trash2 className="h-5 w-5" />
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}
        
        {activeTab === 'add' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Agregar Nuevo Producto</h2>
            <div className="space-y-4">
              <input
                type="text"
                placeholder="Nombre del producto"
                value={nuevoProducto.Nombre_Producto}
                onChange={(e) => setNuevoProducto({ ...nuevoProducto, Nombre_Producto: e.target.value })}
                className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <input
                type="text"
                placeholder="Descripcin"
                value={nuevoProducto.Descripcion}
                onChange={(e) => setNuevoProducto({ ...nuevoProducto, Descripcion: e.target.value })}
                className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <input
                type="number"
                placeholder="Precio"
                value={nuevoProducto.Precio}
                onChange={(e) => setNuevoProducto({ ...nuevoProducto, Precio: parseFloat(e.target.value) })}
                className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <input
                type="number"
                placeholder="Cantidad"
                value={nuevoProducto.Cantidad}
                onChange={(e) => setNuevoProducto({ ...nuevoProducto, Cantidad: parseInt(e.target.value) })}
                className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <select
                value={nuevoProducto.ID_Categoria}
                onChange={(e) => setNuevoProducto({ ...nuevoProducto, ID_Categoria: e.target.value })}
                className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Selecciona una categora</option>
                {categorias.map(categoria => (
                  <option key={categoria.ID_Categoria} value={categoria.ID_Categoria}>{categoria.Nombre_Categoria}</option>
                ))}
              </select>
              <button 
                onClick={addProducto} 
                className="w-full px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <PlusSquare className="inline-block mr-2 h-5 w-5" /> Agregar Producto
              </button>
            </div>
          </div>
        )}
        
        {activeTab === 'analytics' && (
          <div className="space-y-6">
            <div className="grid gap-6 md:grid-cols-2">
              <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
                <h2 className="text-2xl font-semibold mb-4 text-gray-800">Distribucin de Inventario por Categora</h2>
                <ResponsiveContainer width="100%" height={300}>
                  <PieChart>
                    <Pie
                      data={categoryChartData}
                      cx="50%"
                      cy="50%"
                      outerRadius={80}
                      fill="#8884d8"
                      dataKey="value"
                      label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                    >
                      {categoryChartData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              </div>
              <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
                <h2 className="text-2xl font-semibold mb-4 text-gray-800">Cantidad por Producto</h2>
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={productos}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="Nombre_Producto" />
                    <YAxis />
                    <Tooltip />
                    <Bar dataKey="Cantidad" fill="#4299E1" />
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </div>
            <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
              <h2 className="text-2xl font-semibold mb-4 text-gray-800">Resumen del Inventario</h2>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="bg-blue-100 p-4 rounded-lg">
                  <p className="text-lg font-semibold">Nmero total de productos:</p>
                  <p className="text-3xl font-bold text-blue-600">{productos.length}</p>
                </div>
                <div className="bg-green-100 p-4 rounded-lg">
                  <p className="text-lg font-semibold">Valor total del inventario:</p>
                  <p className="text-3xl font-bold text-green-600">{formatCurrency(totalValue)}</p>
                </div>
                <div className="bg-yellow-100 p-4 rounded-lg">
                  <p className="text-lg font-semibold">Productos con stock bajo:</p>
                  <p className="text-3xl font-bold text-yellow-600">{lowStockProductos.length}</p>
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'lowStock' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Productos con Stock Bajo</h2>
            <div className="mb-4">
              <label htmlFor="lowStockThreshold" className="block text-sm font-medium text-gray-700">Umbral de Stock Bajo:</label>
              <input
                type="number"
                id="lowStockThreshold"
                value={lowStockThreshold}
                onChange={(e) => setLowStockThreshold(parseInt(e.target.value))}
                className="mt-1 block w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
              />
            </div>
            {lowStockProductos.length > 0 ? (
              <table className="min-w-full bg-white border border-gray-300">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Nombre</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Cantidad</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Categora</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                  {lowStockProductos.map(producto => (
                    <tr key={producto.ID_Producto} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-no-wrap">{producto.Nombre_Producto}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{producto.Cantidad}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        {categorias.find(cat => cat.ID_Categoria === producto.ID_Categoria)?.Nombre_Categoria}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <p className="text-green-600"><AlertTriangle className="inline-block mr-2 h-5 w-5" /> No hay productos con stock bajo en este momento.</p>
            )}
          </div>
        )}

        {activeTab === 'sales' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Historial de Ventas</h2>
            <div className="mb-4">
              <p className="text-lg font-semibold">Total de ventas: <span className="text-green-600">{formatCurrency(totalSales)}</span></p>
            </div>
            <div className="overflow-x-auto">
              <table className="min-w-full bg-white border border-gray-300">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Fecha</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Producto</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Cantidad</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Total</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                  {ventas.map(venta => (
                    <tr key={venta.ID_Venta} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-no-wrap">{new Date(venta.Fecha_Venta).toLocaleString()}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        {productos.find(p => p.ID_Producto === venta.ID_Producto)?.Nombre_Producto}
                      </td>
                      <td className="px-6 py-4 whitespace-no-wrap">{venta.Cantidad_Vendida}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{formatCurrency(venta.Total_Venta)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {activeTab === 'providers' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Proveedores</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full bg-white border border-gray-300">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Nombre</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Contacto</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                  {proveedores.map(proveedor => (
                    <tr key={proveedor.ID_Proveedor} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-no-wrap">{proveedor.Nombre_Proveedor}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">{proveedor.Contacto_Proveedor}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {activeTab === 'restock' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Historial de Reabastecimiento</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full bg-white border border-gray-300">
                <thead className="bg-gray-100">
                  <tr>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Fecha</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Producto</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Cantidad</th>
                    <th className="px-6 py-3 border-b border-gray-300 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">Proveedor</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-300">
                  {reabastecimientos.map(reabastecimiento => (
                    <tr key={reabastecimiento.ID_Reabastecimiento} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-no-wrap">{new Date(reabastecimiento.Fecha_Reabastecimiento).toLocaleString()}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        {productos.find(p => p.ID_Producto === reabastecimiento.ID_Producto)?.Nombre_Producto}
                      </td>
                      <td className="px-6 py-4 whitespace-no-wrap">{reabastecimiento.Cantidad_Reabastecida}</td>
                      <td className="px-6 py-4 whitespace-no-wrap">
                        {proveedores.find(p => p.ID_Proveedor === reabastecimiento.ID_Proveedor)?.Nombre_Proveedor}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {activeTab === 'settings' && (
          <div className="bg-white shadow-lg rounded-lg p-6 transition-all duration-300 hover:shadow-xl">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">Configuracin</h2>
            <div className="space-y-4">
              <div>
                <label htmlFor="taxRate" className="block text-sm font-medium text-gray-700">Tasa de Impuesto (%):</label>
                <input
                  type="number"
                  id="taxRate"
                  value={settings.taxRate}
                  onChange={(e) => setSettings({...settings, taxRate: parseFloat(e.target.value)})}
                  className="mt-1 block w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                />
              </div>
              <div>
                <label htmlFor="currency" className="block text-sm font-medium text-gray-700">Moneda:</label>
                <select
                  id="currency"
                  value={settings.currency}
                  onChange={(e) => setSettings({...settings, currency: e.target.value})}
                  className="mt-1 block w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                >
                  <option value="CLP">Peso Chileno (CLP)</option>
                  <option value="USD">Dlar Estadounidense (USD)</option>
                  <option value="EUR">Euro (EUR)</option>
                </select>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

export default InventoryManagementSystem